/* üìå Í¥ÄÎ¶¨Ïûê Í≥†Í∞ù ÏßÄÏõê ÌéòÏù¥ÏßÄ */
.admin-support {
    padding: 20px;
    text-align: center;
}

/* üìå Í≤ÄÏÉâ & ÌïÑÌÑ∞ Ïª®Ìä∏Î°§ */
.support-controls {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 15px;
}

/* üìå ÌïÑÌÑ∞ Î≤ÑÌäº Í≥µÌÜµ Ïä§ÌÉÄÏùº */
.support-controls button {
    padding: 10px 14px;
    border-radius: 12px;
    border: 2px solid transparent;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    text-transform: uppercase;
    text-shadow: 0 0 5px rgba(255, 255, 255, 0.6);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.2);
}

/* ‚úÖ ÎÑ§Ïò® Ìö®Í≥º Ï†ÅÏö© */
.all-btn {
    background: #777;
    color: white;
    border-color: #999;
    box-shadow: 0 0 10px rgba(200, 200, 200, 0.7);
}

/* ‚úÖ ÎØ∏ÎãµÎ≥Ä Î≤ÑÌäº (Î∂àÌÉÄÎäî ÎÑ§Ïò® Ïò§Î†åÏßÄ) */
.pending-btn {
    background: linear-gradient(145deg, #ff8c00, #ff5500);
    color: white;
    border-color: #ffb74d;
    box-shadow: 0 0 15px rgba(255, 120, 0, 0.9);
}

.pending-btn:hover {
    background: #ff5a00;
    border-color: #ff9100;
    box-shadow: 0 0 25px rgba(255, 90, 0, 1);
    text-shadow: 0 0 10px rgba(255, 120, 0, 1);
}

.resolved-btn {
    background: #4caf50;
    color: white;
    border-color: #66bb6a;
    box-shadow: 0 0 10px rgba(76, 175, 80, 0.8);
}

/* ‚úÖ ÎãµÎ≥Ä Î≤ÑÌäº (ÏÑ†Î™ÖÌïú ÎÑ§Ïò® Ï¥àÎ°ù) */
.resolved-btn {
    background: linear-gradient(145deg, #4caf50, #1e8e3e);
    color: white;
    border-color: #66bb6a;
    box-shadow: 0 0 15px rgba(76, 175, 80, 0.9);
}

.resolved-btn:hover {
    background: #2e7d32;
    border-color: #388e3c;
    box-shadow: 0 0 25px rgba(76, 255, 80, 1);
    text-shadow: 0 0 10px rgba(76, 255, 80, 1);
}

.support-controls button:hover {
    filter: brightness(1.3);
}

/* üìå Î¨∏Ïùò ÌÖåÏù¥Î∏î */
.support-table {
    width: 100%;
    border-collapse: collapse;
    background: #1a1a1a;
    border-radius: 8px;
    overflow: hidden;
}

.support-table th {
    background: #333;
    color: white;
    font-weight: bold;
    padding: 12px;
    text-align: center;
}

.support-table td {
    padding: 12px;
    text-align: center;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    color: #ddd;
}

/* üìå Î¨∏Ïùò ÎÇ¥Ïö© ÏÖÄ */
.question-cell {
    max-width: 400px;
    word-wrap: break-word;
    white-space: normal;
    overflow-wrap: break-word;
    font-size: 14px;
    padding: 8px;
}

/* üìå ÏÉÅÌÉú Ïä§ÌÉÄÏùº */
.status-pending {
    color: #ff4444;
    font-weight: bold;
}
.status-answered {
    color: #4caf50;
    font-weight: bold;
}

/* üìå ÏÉÅÏÑ∏ Î≥¥Í∏∞ Î≤ÑÌäº (ÎÑ§Ïò® Î∏îÎ£®) */
.view-btn {
    padding: 6px 12px;
    background: #00aaff;
    color: white;
    border: 2px solid #0077cc;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    box-shadow: 0 0 10px rgba(0, 170, 255, 0.8);
    transition: 0.3s;
}

.view-btn:hover {
    background: #0088cc;
    border-color: #0055aa;
    box-shadow: 0 0 15px rgba(0, 136, 255, 0.9);
}

/* üìå Î™®Îã¨ */
.modal {
    position: fixed;
    top: 0; left: 0;
    width: 100%; height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
}

/* üìå Î™®Îã¨ Ïª®ÌÖêÏ∏† */
.modal-content {
    background: #222;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0px 4px 15px rgba(255, 255, 255, 0.2);
    max-width: 500px;
    width: 90%;
    max-height: 600px;
    overflow-y: auto;
}

/* üìå Î¨∏Ïùò ÏÉÅÏÑ∏ ÎÇ¥Ïö© */
.modal-text {
    max-height: 200px;
    overflow-y: auto;
    padding: 12px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    text-align: left;
    word-wrap: break-word;
    width: 100%;
    box-sizing: border-box;
}

/* üìå ÎãµÎ≥Ä ÏûÖÎ†• Ïπ∏ */
.response-textarea {
    width: 100%;
    height: 200px;
    padding: 12px;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.2);
    color: white;
    border: none;
    resize: none;
    box-sizing: border-box;
}

/* üìå Î≤ÑÌäº Ïª®ÌÖåÏù¥ÎÑà */
.modal-buttons {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-top: 15px;
}

/* üìå Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.modal-button {
    padding: 8px 16px;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    border: 2px solid transparent;
    transition: 0.3s;
}

/* üìå ÎãµÎ≥Ä Îì±Î°ù Î≤ÑÌäº (ÎÑ§Ïò® Ï¥àÎ°ù) */
.submit-btn {
    background: #4caf50;
    color: white;
    border-color: #66bb6a;
    box-shadow: 0 0 10px rgba(76, 175, 80, 0.8);
}

.submit-btn:hover {
    background: #388e3c;
    border-color: #2e7d32;
    box-shadow: 0 0 15px rgba(56, 142, 60, 0.9);
}

/* üìå Îã´Í∏∞ Î≤ÑÌäº (ÎÑ§Ïò® Î†àÎìú) */
.close-btn {
    background: #ff4d4d;
    color: white;
    border-color: #ff6666;
    box-shadow: 0 0 10px rgba(255, 77, 77, 0.8);
}

.close-btn:hover {
    background: #cc3a3a;
    border-color: #ff3333;
    box-shadow: 0 0 15px rgba(255, 51, 51, 0.9);
}
