/* üìå ÏÇ¨Ïö©Ïûê Í¥ÄÎ¶¨ ÌéòÏù¥ÏßÄ */
.user-management {
    padding: 20px;
}

/* üìå Í≤ÄÏÉâ & Ï†ïÎ†¨ Ïª®Ìä∏Î°§ */
.user-controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
    padding: 10px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    gap: 10px;
}

/* üìå ÏûÖÎ†• ÌïÑÎìú & ÎìúÎ°≠Îã§Ïö¥ Í≥µÌÜµ Ïä§ÌÉÄÏùº */
.user-controls input,
.user-controls select {
    padding: 10px;
    border-radius: 8px;
    border: none;
    background-color: rgba(255, 255, 255, 0.2);
    color: white;
    font-size: 14px;
    transition: 0.3s;
}

.user-controls input {
    flex: 1;
}

.user-controls input:focus,
.user-controls select:focus {
    background-color: rgba(255, 255, 255, 0.3);
    outline: none;
}

.user-controls select {
    min-width: 140px;
    cursor: pointer;
}

.user-controls select option {
    background-color: #333;
    color: white;
    padding: 10px;
}

/* üìå ÏÇ¨Ïö©Ïûê Î™©Î°ù ÌÖåÏù¥Î∏î */
.user-table {
    width: 100%;
    border-collapse: collapse;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    overflow-x: auto;
}

/* üìå ÌÖåÏù¥Î∏î ÏÖÄ Ïä§ÌÉÄÏùº */
.user-table th,
.user-table td {
    padding: 12px;
    text-align: center;
    word-break: break-word;
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.user-table th {
    background: rgba(255, 255, 255, 0.15);
    font-weight: bold;
}

/* üìå ÌÖåÏù¥Î∏î hover Ìö®Í≥º */
.user-table tr:hover {
    background: rgba(255, 255, 255, 0.15);
    transition: 0.3s;
}

/* üìå Î≤ÑÌäº Í≥µÌÜµ Ïä§ÌÉÄÏùº */
.user-table button,
.action-button {
    padding: 8px 12px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    font-size: 14px;
    transition: all 0.3s ease;
    margin-right: 5px;
}

/* ‚úÖ Î≥¥Í∏∞ Î≤ÑÌäº (ÎÑ§Ïò® Î∏îÎ£®) */
.view-button {
    background: #007BFF;
    color: white;
    border: 2px solid #00FFFF;
    box-shadow: 0 0 10px rgba(0, 255, 255, 0.7);
    font-weight: bold;
}

.view-button:hover {
    background: #0056b3;
    border-color: #00E5FF;
    box-shadow: 0 0 15px rgba(0, 229, 255, 0.8);
}


/* ‚úÖ ÎÑ§Ïò® Ï£ºÌô© (ÎπÑÌôúÏÑ±Ìôî Î≤ÑÌäº) */
.deactivate-button {
    background: #FF9800;
    color: white;
    border: 2px solid #E65100;
    box-shadow: 0 0 8px rgba(255, 152, 0, 0.8);
    font-weight: bold;
}

.deactivate-button:hover {
    background: #E65100;
    border-color: #BF360C;
}

/* ‚úÖ ÎÑ§Ïò® ÎÖπÏÉâ (ÌôúÏÑ±Ìôî Î≤ÑÌäº) */
.activate-button {
    background: #4CAF50;
    border: 2px solid #388E3C;
    box-shadow: 0 0 8px rgba(76, 175, 80, 0.8);
}

.activate-button:hover {
    background: #388E3C;
    border-color: #2E7D32;
}

/* ‚úÖ ÎÑ§Ïò® ÌïëÌÅ¨ (ÏÇ≠Ï†ú Î≤ÑÌäº) */
.delete-button {
    background: #ff007f;
    border: 2px solid #ff00ff;
    box-shadow: 0 0 10px rgba(255, 0, 255, 0.8);
    font-weight: bold;
}

.delete-button:hover {
    background: #cc0066;
    border-color: #ff00cc;
}

/* ‚úÖ ÌôúÏÑ±ÌôîÎêú ÏÉÅÌÉú */
.status-active {
    color: #4CAF50; /* ÎÖπÏÉâ */
    font-weight: bold;
}

/* ‚úÖ ÎπÑÌôúÏÑ±ÌôîÎêú ÏÉÅÌÉú */
.status-inactive {
    color: #FF9800; /* Ï£ºÌô©ÏÉâ */
    font-weight: bold;
}

/* ‚úÖ ÌéòÏù¥ÏßÄÎÑ§Ïù¥ÏÖò Ïä§ÌÉÄÏùº */
.pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
}

.pagination button {
    background-color: #1a1a1a;
    color: #fff;
    border: 2px solid #444;
    border-radius: 8px;
    padding: 8px 14px;
    margin: 0 6px;
    cursor: pointer;
    font-size: 14px;
    transition: all 0.3s ease;
}

.pagination button:hover {
    background-color: #333;
    border-color: #777;
}

.pagination button.active {
    background-color: #ff007f;
    border-color: #ff00ff;
    font-weight: bold;
    box-shadow: 0 0 10px rgba(255, 0, 255, 0.8);
}

.pagination button:disabled {
    background-color: #222;
    border-color: #444;
    color: #777;
    cursor: not-allowed;
}
