/* üìå ÎåÄÏãúÎ≥¥Îìú Ï†ÑÏ≤¥ Ï†ïÎ†¨ */
.admin-dashboard {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}

/* üìå ÎåÄÏãúÎ≥¥Îìú Ïπ¥Îìú + Ï∞®Ìä∏ Ïª®ÌÖåÏù¥ÎÑà */
.dashboard-grid {
    display: flex;
    flex-wrap: wrap; /* ‚úÖ Î∞òÏùëÌòï ÏûêÎèô Ï§ÑÎ∞îÍøà */
    justify-content: center;
    gap: 20px;
    width: 100%;
}

/* üìå Í∞úÎ≥Ñ Î∞ïÏä§ (Ïπ¥Îìú + Ï∞®Ìä∏) */
.dashboard-box {
    background: #1a1a1a;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    flex: 1 1 300px; /* ‚úÖ ÏµúÏÜå 300px, ÌôîÎ©¥ ÌÅ¨Í∏∞Ïóê Îî∞Îùº ÏûêÎèô Ï°∞Ï†ï */
    max-width: 400px; /* ‚úÖ ÎÑàÎ¨¥ ÎÑìÏñ¥ÏßÄÏßÄ ÏïäÎèÑÎ°ù Ï†úÌïú */
}

/* üìå Í∞úÎ≥Ñ Ïπ¥Îìú */
.dashboard-card {
    background: #2c2c2c;
    padding: 15px;
    border-radius: 8px;
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%;
    text-align: center;
    color: #fff;
    transition: 0.3s;
}

.dashboard-card:hover {
    background: #3a3a3a;
}

/* üìå Í∞úÎ≥Ñ Ï∞®Ìä∏ */
.dashboard-chart {
    background: #1e1e1e;
    padding: 15px;
    border-radius: 8px;
    width: 100%;
    min-width: 250px;
}

/* üìå Ï∞®Ìä∏ ÌÅ¨Í∏∞ Ï°∞Ï†ï */
.dashboard-chart canvas {
    width: 100% !important;
    height: auto !important;
}

/* üìå ÌïòÎã® Ï∂îÍ∞Ä ÏΩòÌÖêÏ∏† (ÏµúÍ∑º Í∞ÄÏûÖ Ïú†Ï†Ä, Ïù∏Í∏∞ Ïä§Ìä∏Î¶¨Î∞ç Í≥°, Í¥ÄÎ¶¨Ïûê ÏïåÎ¶º) */
.dashboard-extra {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    width: 100%;
    margin-top: 20px;
}

/* üìå Ï∂îÍ∞Ä ÏΩòÌÖêÏ∏† Í∞úÎ≥Ñ Î∞ïÏä§ */
.dashboard-section {
    background: #1e1e1e;
    padding: 15px;
    border-radius: 8px;
    flex: 1 1 300px; /* ‚úÖ ÏµúÏÜå 300px, ÏµúÎåÄ ÌÅ¨Í∏∞ ÏûêÎèô Ï°∞Ï†ï */
    max-width: 400px;
    color: #fff;
}

/* üìå ÏÑπÏÖò ÌÉÄÏù¥ÌãÄ */
.dashboard-section h3 {
    text-align: center;
    margin-bottom: 10px;
}

/* üìå Î¶¨Ïä§Ìä∏ Ïä§ÌÉÄÏùº */
.dashboard-section ul {
    list-style: none;
    padding: 0;
}

.dashboard-section li {
    padding: 8px;
    border-bottom: 1px solid #444;
    display: flex;
    align-items: center;
    gap: 10px;
}

/* üìå ÏïÑÏù¥ÏΩò ÌÅ¨Í∏∞ */
.dashboard-icon {
    font-size: 24px;
}

.dashboard-section li svg {
