}

/* üìå Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.link-button {
    background: none;
    border: none;
    color: white;
    font-size: 16px;
    cursor: pointer;
    text-decoration: none;
    padding: 0;
    display: flex;
    align-items: center;
    gap: 10px;
    width: 100%;
    text-align: left;
}

/* üìå Î≤ÑÌäº Ìò∏Î≤Ñ Ìö®Í≥º */
.link-button:hover {
    text-decoration: underline;
}
