/* üìå Ïã†Í≥† Í¥ÄÎ¶¨ ÌéòÏù¥ÏßÄ */
.report-management {
    text-align: center;
    padding: 20px;
}

/* üìå Ïã†Í≥† Î™©Î°ù & ÏÉÅÏÑ∏ ÎÇ¥Ïö© Ïª®ÌÖåÏù¥ÎÑà */
.report-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
    margin-right: 100px;
}

/* üìå Ïã†Í≥† Î™©Î°ù */
.report-list {
    flex: 1 1 ;
    display: flex;
    flex-direction: column;
    gap: 10px;
    min-width: 300px;
    max-width: 800px;
}

/* üìå Ïã†Í≥† ÎÇ¥Ïö© Î∞ïÏä§ (ÎÑ§Ïò® Î∏îÎ£® ÌÖåÎëêÎ¶¨) */
.report-details {
    background: rgba(255, 255, 255, 0.05);
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 10px;
    border: 2px solid rgba(0, 170, 255, 0.7);
    box-shadow: 0 0 10px rgba(0, 170, 255, 0.6);
    transition: all 0.3s ease-in-out;
}

.report-details:hover {
    box-shadow: 0 0 15px rgba(0, 170, 255, 1);
}

/* üìå Ïã†Í≥†Îêú Í≤åÏãúÍ∏Ä Î∞ïÏä§ (ÎÑ§Ïò® Ïò§Î†åÏßÄ ÌÖåÎëêÎ¶¨) */
.post-info {
    background: rgba(255, 255, 255, 0.05);
    padding: 15px;
    border-radius: 10px;
    border: 2px solid rgba(255, 0, 0, 0.7);
    box-shadow: 0 0 10px rgba(255, 0, 0, 0.6);
    transition: all 0.3s ease-in-out;
}

.post-info:hover {
    box-shadow: 0 0 15px rgb(255, 0, 0);
}

.report-list ul {
    list-style-type: none;
    padding: 0;
}

.report-list li {
    padding: 12px;
    border-bottom: 1px solid rgba(245, 240, 240, 0.2);
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
    transition: background 0.2s ease-in-out;
    margin-bottom: 8px; /* ‚úÖ Ïã†Í≥† Ìï≠Î™© Í∞ÑÍ≤© Ï∂îÍ∞Ä */
}

.report-list li:hover {
    background: rgba(255, 255, 255, 0.1);
}

/* üö® Ïã†Í≥† ÏÉÅÌÉúÎ≥Ñ Ïä§ÌÉÄÏùº */
.pending-report { background-color: #da7800 !important; } /* Ïã†Í≥† Ï†ëÏàò */
.resolved-report { background-color: #2E8B57 !important; } /* Ìï¥Í≤∞Îêú Ïã†Í≥† */
.banned-report { background-color: #D32F2F !important; } /* Ï†ïÏßÄÎêú ÏÇ¨Ïö©Ïûê */

/* üìå ÏÑ†ÌÉùÎêú Ïã†Í≥† Ìï≠Î™© */
.selected {
    border: 3px solid white !important;
}

/* üìå Ïã†Í≥† ÏÉÅÏÑ∏ ÎÇ¥Ïö© */
.report-detail {
    flex: 1 1 45%;
    background-color: #222;
    padding: 20px;
    border-radius: 10px;
    min-height: 200px;
    max-width: 500px;
}

/* ‚úÖ Ïã†Í≥† Í¥ÄÎ¶¨ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.report-actions {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 20px;
}

/* ‚úÖ Ï†ïÏßÄ Ìï¥Ï†ú Î≤ÑÌäº (ÎÑ§Ïò® Î∏îÎ£®) */
.unban-button {
    padding: 10px;
    font-size: 16px;
    background: #007BFF;
    color: white;
    border: 2px solid #00FFFF;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0, 226, 226, 0.8);
    transition: all 0.3s ease;
}

.unban-button:hover {
    background: #0056b3;
    border-color: #00E5FF;
    box-shadow: 0 0 15px rgb(1, 204, 226);
}

/* ‚úÖ Ï†ïÏßÄ Ï†ÅÏö© Î≤ÑÌäº (ÎÑ§Ïò® Î†àÎìú) */
.ban-button {
    padding: 10px 14px;
    font-size: 16px;
    background: #ff4444;
    color: white;
    border: 2px solid #ff6666;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(255, 77, 77, 0.9);
    transition: all 0.3s ease;
}

.ban-button:hover {
    background: #cc3a3a;
    border-color: #ff3333;
    box-shadow: 0 0 15px rgba(255, 51, 51, 1);
}

/* ‚úÖ ÏÇ¨Ïú† Î∂àÏ∂©Î∂Ñ Î≤ÑÌäº (ÎÑ§Ïò® Ïò§Î†åÏßÄ) */
.insufficient-reason-button {
    padding: 10px 14px;
    font-size: 16px;
    background: #ff9800;
    color: white;
    border: 2px solid #ffcc80;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(255, 152, 0, 0.9);
    transition: all 0.3s ease;
}

.insufficient-reason-button:hover {
    background: #e65100;
    border-color: #ff6f00;
    box-shadow: 0 0 15px rgba(255, 111, 0, 1);
}

/* üìå Î∞òÏùëÌòï Ïä§ÌÉÄÏùº */
@media (max-width: 768px) {
    .report-container {
        flex-direction: column;
        align-items: center;
    }

    .report-list, .report-detail {
        flex: 1 1 100%;
        max-width: 100%;
    }

    .report-actions {
        width: 100%;
        align-items: center;
    }
}
